.App {
  text-align: center;
}

.App-logo {
  height: min(18vmin,90px);
  pointer-events: none;
}

.App-logo-loader {
  height: min(33vmin,168px);
  pointer-events: none;
}

.App-logo-err {
  height: min(32vmin,168px);
  pointer-events: none;
}

