.App-header {
  /* background-color: #282c34; */
  height: 14.5vh;
  max-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container-header {
  display: flex;
  justify-content: flex-start;
  margin-top: max(28px, 2.5%);
}

.row-styling {
  text-align: left;
  padding-right: 5%;
  overflow-wrap: break-word;
  padding-top: 1.1%;
}

.title {
  margin-top: 6px !important;
  /* margin-bottom: 1.5% !important; */
  text-shadow: 2px 2px #D5D7DB;
  font-weight:550;
}


.title-div {
  padding-left: 4%;
  padding-right: 4%;
}


.ui.table { background-color: #f8f8f8 !important;  }

.ui.table thead th {font-weight: 600 !important;}



tbody, td, tfoot, th, thead, tr {
  /* border-width: 1px !important;
  border-style: solid !important;
  border-color: rgba(110, 110, 102,0.4) !important; */
  /* background-color: #fcf7c7; */
  background-color: rgba(214, 233, 225 ,0.4);
}


.table>:not(:last-child)>:last-child>* {
  border-bottom-color: none;
  background-image: linear-gradient(rgba(5, 104, 57 ,0.78),rgba(5, 104, 57 ,0.95));
}

.sr-only {
  display: none;
}

.col-dose {
  display: flex;
  justify-content : center;
  align-items: center;
  background-image: linear-gradient(rgba(5, 104, 57 ,0.75),rgba(5, 104, 57 ,1));
  color: white;
  border-radius: 6px 0 0 6px;
}

.col-vc-detail {
  padding-left: 4.5% !important;
  /* background-color: #f3f2f2; */
  background-image: linear-gradient(rgba(214, 233, 225 ,0.75),rgba(214, 233, 225  ,1));
  border-radius: 0 6px 6px 0;
}

.row-vc-detail{
  margin-bottom: 2%;
  overflow-wrap: break-word;
}

.detail-title{
  font-weight: 550;
}

.detail-txt-focus{
  font-weight: 550;
  color: rgb(9, 73, 43);
}

.info-title {
  font-weight: 550;
  /* margin-top: 3%; */
}

.gray {
  color: gray;
  /* margin-top: 3%; */
}

.info-title-strip {
  font-weight: 530;
  text-shadow: 2px 2px #145220;
  color: white;
  padding-left: 5px;
}

.table-title{
  color:#FEFFFE;
  text-shadow: 2px 2px #145220;
}

.table-titleTH{
  color:#FEFFFE;
  text-shadow: 2px 2px #145220;
  font-weight: 550;
}

.icon-align{
  display: flex;
  justify-content: center !important;
  text-align: center;
}


