.table-dose-title {
  padding: 1% 1% 1% 1%;
  text-shadow:2px 2px #D5D7DB ;
}


.cell-styling {
  overflow-wrap: break-word;
}

.info-row {
  margin-left:min(5%,55px) !important;
  margin-right:"2%" !important;
}


.strip-bar{
  background-image: linear-gradient(rgba(5, 104, 57 ,0.75),rgba(5, 104, 57 ,0.9));
  border-radius: 0 11px 11px 0;
  padding: min(1.2%,9px);
}